<template>
  <div id="homeIndex" class="home-index" style="flex: 1;height: 0;">
    <header class="page-header" id="top">
      <div class="page-header-container">
        <a href="" class="page-header-brand">
          <img :src="require('@/assets/svg/sdog.svg')" alt="SDOG" class="page-header-brand-img">
        </a>
        <div class="page-header-content">
          <a href="#" class="page-header-toggle collapsed" data-toggle="navbar">
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
            <span class="icon-bar"></span>
          </a>
          <nav class="page-header-navbar">
            <ul class="nav page-header-nav">
              <li>
                <a href="#whitelist" data-toggle="scroll-to" class="nav-link">空投机制</a>
              </li>
              <li>
                <a href="#mining-mechanism" data-toggle="scroll-to" class="nav-link">挖矿机制</a>
              </li>
              <li>
                <a href="#token-mechanism" data-toggle="scroll-to" class="nav-link">代币机制</a>
              </li>
              <li>
                <a href="buy" class="btn btn-primary btn-tr process">领取流程</a>
              </li>
            </ul>
          </nav>
        </div>
      </div>
    </header>
    <!--    <transition name="fade">-->
    <welcome></welcome>
    <!--    </transition>-->
    <!--    <transition name="fade">-->
    <airdrop></airdrop>
    <!--    </transition>-->
    <mining></mining>
    <token></token>
    <home-footer></home-footer>
    <div id="loading" style="display: none;">
      <img :src="require('@/assets/image/loading.gif')">
    </div>
  </div>
</template>

<script>
import welcome from "@/views/home/welcome";
import Airdrop from "@/views/home/airdrop";
import Mining from "@/views/home/mining";
import Token from "@/views/home/token";
import HomeFooter from "@/views/home/footer";
import $ from 'jquery'
import mixins from "@/views/home/mixins";
export default {
  name: "index",
  components: {
    HomeFooter,
    Token,
    Mining,
    Airdrop,
    welcome
  },
  mixins: [mixins],
  data() {
    return {
      isWelcome: false,
      isAirdrop: false
    }
  },
  mounted() {
    this.readyLoad()
    this.loadAnimation()
    this.loadScrollReady()
  },
  methods: {
    readyLoad() {
      const _that = this
      $('[data-toggle="scroll-to"]').click(function () {
        var target = $(this).attr('href');
        if ($(target).length > 0 && !$(this).hasClass('disabled')) {
          _that.scrollToElement(target, 600);
          return false;
        }
      })
    },
    scrollToElement(target, time) {
      if ($(target).length > 0 && !$(this).hasClass('disabled')) {
        var offset = $(target).offset();
        $('html, body').animate({
              scrollTop: offset.top
            },
            time);
        if ($('body').hasClass('open-navbar')) {
          $('.page-header-toggle').addClass('collapsed');
          $('.page-header-navbar').removeClass('in');
          $('body').removeClass('open-navbar');
        }
      }
    }
  }
}
</script>

<style scoped>
.page-layout-btn {
  padding: 0.6rem 2rem;
  background: #e69564;
  font-size: 1rem;
  font-weight: 500;
  color: white;
  border-radius: 1.5rem;
  margin-bottom: 2rem;
  display: block;
  text-align: center;
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 1rem;
}
</style>